<template>
    <div class="goods">
        <!-- 头部返回 -->
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <!-- 头部导航 -->
        <van-tabs v-model="activeName" @change="goTab">
            <van-tab v-for="item in navList.navData" :name="item.name" :key="item.id" :title="item.name ">
                <div class="goodsTitle">
                    <h4>{{item.name}}
                        <p>{{item.front_desc}}</p>
                    </h4>
                </div>
                <!-- 商品列表 -->
                <div class="goodsList">
                    <div class="goodsItem" v-for="item in goodList" :key="item.id" @click="goInfo(item.id)">
                        <img :src="item.list_pic_url" alt="">
                        <p>{{item.name}}</p>
                        <span>￥{{item.retail_price}}</span>
                    </div>
                </div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
    import {
        categoryNav,
        goodsList
    } from "@/api/category/goods/index.js"
    export default {
        data() {
            return {
                navList: [],
                goodList: [],
                activeName: '',
            };
        },
        computed: {

        },
        created() {

        },
        mounted() {
            categoryNav({
                id: this.$route.query.id
            }).then(res => {
                this.activeName = res.data.currentNav.name
                this.navList = res.data
            });
            goodsList({
                categoryId: this.$route.query.id
            }).then(res => {
                this.goodList = res.data.data
            })
        },
        methods: {
            goTab(name, title) {
                console.log(name, title);
                goodsList({
                    categoryId: this.navList.navData.find(ele => ele.name == name).id
                }).then(res => {
                    this.goodList = res.data.data
                })
            },
            ss() {
                console.log(this.navList);
            },
            onClickLeft() {
                history.back()
            },
            goInfo(id){
                this.$router.push(`/category/goods/info?id=${id}`)
            }
        },
    };
</script>

<style scoped lang="scss">
    .ss {
        position: absolute;
        top: 100px;
        right: 20px;
    }

    .goodsTitle {
        margin-top: 10px;
        margin-bottom: 5px;
        width: 100%;
        text-align: center;
        height: 120px;
        background-color: #fff;
        display: flex;
        align-items: center;

        h4 {
            width: 100%;
            margin-bottom: .24rem;
            font-size: .4rem;
            color: #333;
            font-weight: normal;

            p {
                margin-top: 10px;
                height: .32rem;
                font-size: .32rem;
                color: #999;
            }
        }
    }

    .goodsList {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;

        .goodsItem {
            width: 49%;
            background-color: #fff;
            margin-bottom: 5px;

            img {
                display: block;
                margin: 0px auto;
                width: 151px;
                height: 151px;
            }

            p {
                width: 100%;
                text-align: center;
                margin: 5px 0;
            }

            span {
                display: block;
                width: 100%;
                text-align: center;
                font-size: .4rem;
                color: #b4282d;
                margin: 20px 0;
            }
        }
    }
</style>